.footer-content{
  position: relative;
  top: 80px;
}

.footer-text {
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 15px;
  border-top: 1px solid #000;
}

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  padding:0;
  padding-right:20px;
  padding-left:0px;
}

.social li {
  list-style: none;
}

.social li a {
  display: inline-block;
  margin-left: 15px;
  background-color: #222;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: 0.2% ease-in-out;
}

.social li a:hover {
  background-color: #f0bc1d;
  transform: translateY(-10px);
}

.social li a img {
  filter: invert(1);
  transform: scale(0.5);
}
